html, body {
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  width: 100%;
  display: table; }

.title-container {
  text-align: center;
  padding-top: 100px;
  background-color: rgb(13, 11, 22) !important;
  height: 100vh;
  box-shadow: 0 1px 2px 1px rgba(0, 0, 0, 0.5);
  position: relative;
  z-index: 1000; }

.content {
  display: inline-block;
  font-weight: 100;
  font-family: 'Lato';
  color: white; }

form {
  font-weight: 300;

  input[type='text'] {
    text-align: center;
    font-size: 24px;
    height: 50px; } }

.title {
  font-size: 96px;
  margin-bottom: 50px; }

.searching {
  font: {
    size: 40px;
    weight: 100; }
  text-align: center;
  margin-top: 100px;
  display: none; }

.tvshow-wrap {
  display: none;
  position: relative; }

.tvshow {
  display: none;
  height: 500px;
  z-index: -1;
  transition: background 1.5s; }

.info {
  font-size: 42px; }

.block {
  position: absolute;
  box-shadow: 0 -1px 2px 1px rgba(0, 0, 0, 0.25);
  z-index: 1000; }
